import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import app from '../App.vue'
import home from '../page/home/index.vue'
const routes = [
  {
    path: '/',
    component: app,
    children: [
      {
        path: '',
        component: home
      }
    ]
  }
]
var router = new VueRouter({routes})
export {router}



